.student-media, .student-media-mv{
    display: flex;
    z-index: 11;
    align-items: center;

    @include tab{
        flex-direction: column;
    }
}
.student-media{
    &__image{
        width: 15rem;
        height: 15rem;
        margin-right: 2rem;
        @include wide-pc{
            width: 150px;
            height: 150px;
            margin-right: 30px;
        }
        @include tab{
            margin-right: 0;
            margin-bottom: 1.2rem;
        }
    }
    &__body{
    }
    &__student-name{
        display: inline-block;
        text-align: center;
        background-color: $color-white;
        padding: 0 0.6rem;
        font-size: 1.2rem;
        height: 2rem;
        line-height: 2rem;
        margin-bottom: 1rem;
        @include wide-pc{
            padding: 0 6px;
            height: 20px;
            font-size: 12px;
            line-height: 20px;
            margin-bottom: 4px;
        }
        @include tab{
            margin: 0 auto;
            margin-bottom: 0;
        }
    }
    &__title{
        color: $color-white;
        font-size: 2.2rem;
        line-height: 1.6;
        margin-bottom: 1.4rem;
        @include wide-pc{
            font-size: 22px;
            margin-bottom: 14px;
        }
        @include tab{
            font-size: 2rem;
            margin-bottom: 1.2rem;
        }
    }
    &__tag{
        color: $color-white;
        margin-bottom: 1.8rem;
        @include wide-pc {
            margin-bottom: 18px;
        }
        @include tab{
            margin-bottom: 1.2rem;
        }
        i{
            font-size: 1.3rem;
            @include wide-pc{
                font-size: 13px;
            }
        }
    }
    &__tag-link{
        font-size: 1.5rem;
        @include wide-pc{
            font-size: 15px;
        }
        @include tab{
            font-size: 1.2rem;
        }
    }
    &__link{
        margin-left: auto;
        @include tab{
            margin-left: 0;
            margin-top: 1.6rem;
        }
    }
}
.student-media-mv{
    .student-media{
        &__image{
            width: 20rem;
            height: 20rem;
            margin-right: 3rem;
            @include wide-pc{
                width: 200px;
                height: 200px;
                margin-right: 30px;
            }
            @include tab{
                margin-right: 0;
                margin-bottom: 1.2rem;
            }
        }
        &__student-name{
            margin-bottom: 0.4rem;
        }
        &__title{
            font-size: 3rem;
            line-height: 1.2;
            margin-bottom: 1.8rem;
            @include wide-pc{
                font-size: 30px;
                margin-bottom: 18px;
            }
            @include tab{
                font-size: 2.4rem;
                line-height: 1.6;
            }
        }
    }
}
.link-btn{
    padding: 0 2rem;
    height: 4rem;
    background-color: $color-black;
    color: $color-white;
    font-size: 1.4rem;
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content:center;
    border-radius: 2rem;
    @include wide-pc{
        padding: 0 20px;
        height: 40px;
        font-size: 14px;
        border-radius:40px;
    }
    i{
        font-size: 1.7rem;
        margin-right: 0.7rem;
        @include wide-pc{
            font-size: 17px;
            margin-right: 7px;
        }
    }
}
.media-margin-bottom{
    margin-bottom: 5rem;
    @include wide-pc{
        margin-bottom: 50px;
    }
    @include tab{
        margin-bottom: 3rem;
    }
}
.meta-info-margin-right{
    margin-right: 0.5rem;
    @include wide-pc{
        margin-right: 5px;
    }
}